import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import css from "./style.module.css";
import Button from "../../components/General/Button";
import * as actions from "../../redux/action/signUpActions";
import Spinner from "../../components/General/Spinner";
import { Redirect } from "react-router-dom";
const SingUpPage = (props) => {
  const [email, setEmail] = useState("");
  const [password1, setPassword1] = useState("");
  const [password2, setPassword2] = useState("");
  const [error, setError] = useState("");

  const signUp = () => {
    if (password1 === password2) {
      setError("");

      props.singupUser(email, password1);
    } else {
      setError("Баталгаажуулах нууц үг хоорондоо таарахгүй байна!");
    }
  };

  return (
    <div className={css.SignUp}>
      {props.userId && <Redirect to="/" />}
      <div>Та Имейл хаягаар бүртгүүлнэ үү ...</div>

      <input
        type="text"
        placeholder="Имейл хаяг"
        onChange={(e) => setEmail(e.target.value)}
      />
      <input
        onChange={(e) => setPassword1(e.target.value)}
        type="password"
        name="password"
        placeholder="Нууц үг"
      />
      <input
        type="password"
        name="password"
        onChange={(e) => setPassword2(e.target.value)}
        placeholder="Дахин нууц оруулна уу"
      />
      {error && <div style={{ color: "red" }}>{error}</div>}
      {props.firebaseError && (
        <div style={{ color: "red" }}>{props.firebaseError.message}</div>
      )}
      {props.saving && <Spinner />}
      <Button text="БҮРТГҮҮЛЭХ" clicked={signUp} buttonType="Success" />
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    saving: state.signupLoginReducer.saving,
    firebaseError: state.signupLoginReducer.firebaseError,
    userId: state.signupLoginReducer.userId,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    singupUser: (email, password) =>
      dispatch(actions.singupUser(email, password)),
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(SingUpPage);
